@import './colors.sass';

.drawer.hidden {
  right: -70vw; }

.drawer {
  height: 100vh;
  width: 70vw;
  background-image: radial-gradient(50% 48%, $brandGold -150%, white 780%);
  position: fixed;
  top: 0;
  right: 0vw;
  z-index: 1;
  transition: right 1s;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: -3px 0px 11px 0px rgba(0, 0, 0, 0.75);
  border-radius: 20px 0px 0px 20px;
  .slideHeader {
    height: 25vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2D3300f8;
    border-radius: 20px 0px 0px 0px;
    span {
      color: white;
      font-size: 40pt;
      font-weight: 100; } }
  section {
    &:first-of-type, &:nth-of-type(2), &:last-of-type {
      height: 25vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .sectionHeader {
        font-size: 16pt;
        display: block;
        margin-bottom: 10px;
        color: white;
        font-weight: 300;
        font-style: italic; }
      .slidebtnContainer {
        display: flex;
        justify-content: center; }
      .slidebtnContainer.first {
        .hoc {
          &:last-of-type {
            margin-left: 30px; } } }
      .slidebtnContainer.last {
        .hoc {
          &:nth-of-type(2) {
            margin-left: 30px;
            margin-right: 30px; } } } }
    &:first-of-type {
      background-color: #4E560C; }
    &:nth-of-type(2) {
      background-color: #8C963F; }
    &:last-of-type {
      background-color: #B2BC66;
      border-radius: 0px 0px 0px 20px; } } }

.slideOverlay.hidden {
  opacity: 0;
  left: -100vw;
  transition: opacity 1s, left .1s ease-in 1s; }

.slideOverlay {
  width: 100vw;
  height: 100vh;
  background-color: #707a238f;
  position: fixed;
  top: 0;
  left: 0;
  transition: opacity 1s, left .1s ease-in;
  opacity: 1; }

@media #{$tablet} {
  .drawer {
    width: 50vw; } }

@media #{$desktop} {
  .drawer {
    height: 80vh;
    width: 26vw;
    bottom: 0;
    top: inherit; } }
