@import './colors.sass';

.popout {
  background-image: radial-gradient(50% 48%, #192f00 -150%, white 780%); }
.popoutContainer {
  padding-left: 26px;
  padding-right: 26px;
  padding-top: 30px;
  padding-bottom: 30px;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-shadow: -3px 0px 11px 0px rgba(0, 0, 0, 0.75);
  border-radius: 20px 20px 0px 0px;
  min-height: 70vw; }

.popoutHeadImg {
  max-width: 36vw; }

.popoutHeading {
  color: white;
  text-align: left;
  font-size: 4vw;
  font-weight: 100; }

.popoutbtnsContainer {
  display: flex;
  .hoc {
    &:nth-of-type(2) {
      margin-left: 30px;
      margin-right: 30px; } } }

@media #{$tablet} {
  .popoutHeadImg {
    width: 20vw !important; } }

@media #{$desktop} {
  .popoutHeadImg {
    width: 14vw !important; }
  .popoutContainer {
    min-height: 40vw; }
  .popoutHeading {
    font-size: 1.6vw;
    font-weight: 300; } }
