@import './colors';
.logoContainer {
  width: 50vw;
  margin-left: 25vw;
  padding-top: 10%;
  height: 20%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    position: absolute;
    animation: heartbeat 3s infinite cubic-bezier(0.25, 0.46, 0.45, 0.94);
    &:nth-of-type(1) {
      width: 8vw;
      animation-delay: .1s; }
    &:nth-of-type(2) {
      width: 10vw;
      animation-delay: .2s; }
    &:nth-of-type(3) {
      width: 12vw;
      animation-delay: .3s; }
    &:nth-of-type(4) {
      width: 14vw;
      animation-delay: .4s; }
    &:nth-of-type(5) {
      width: 16vw;
      animation-delay: .5s; }
    &:nth-of-type(6) {
      width: 18vw;
      animation-delay: .6s; }
    &:nth-of-type(7) {
      width: 20vw;
      animation-delay: .7s; }
    &:nth-of-type(8) {
      width: 22vw;
      animation-delay: .8s; }
    &:nth-of-type(9) {
      width: 24vw;
      animation-delay: .9s; }
    &:nth-of-type(10) {
      width: 26vw;
      animation-delay: 1s; }
    &:nth-of-type(11) {
      width: 28vw;
      animation-delay: 1.1s; }
    &:nth-of-type(12) {
      width: 30vw;
      animation-delay: 1.2s; }
    &:nth-of-type(13) {
      width: 34vw;
      animation-delay: 1.3s; } } }

@media #{$desktop} {
  .logoContainer {
    padding-top: 5vh;
    img {
      &:nth-of-type(1) {
        width: 2vw; }
      &:nth-of-type(2) {
        width: 3vw; }
      &:nth-of-type(3) {
        width: 4vw; }
      &:nth-of-type(4) {
        width: 5vw; }
      &:nth-of-type(5) {
        width: 6vw; }
      &:nth-of-type(6) {
        width: 7vw; }
      &:nth-of-type(7) {
        width: 8vw; }
      &:nth-of-type(8) {
        width: 9vw; }
      &:nth-of-type(9) {
        width: 10vw; }
      &:nth-of-type(10) {
        width: 11vw; }
      &:nth-of-type(11) {
        width: 12vw; }
      &:nth-of-type(12) {
        width: 13vw; }
      &:nth-of-type(13) {
        width: 14vw; } } } }

@keyframes heartbeat {
  0% {
    transform: scale( .30 ); }
  20% {
    transform: scale( .8 );
    filter: drop-shadow(10px 0px 10px white); }
  40% {
    transform: scale( .30 ); }
  60% {
    transform: scale( 1.2 );
    filter: drop-shadow(-10px 0px 10px white); }
  80% {
    transform: scale( .30 ); }
  100% {
    transform: scale( .30 );
    filter: drop-shadow(0px 0px 0px green); } }
