@import './colors.sass';

.actionButton {
  width: 14vw;
  height: 14vw;
  background-color: #707a2387;
  position: fixed;
  bottom: 80px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  transition: right 1s;
  box-shadow: 3px 4px 11px 0px rgba(0,0,0,0.75); }
.hiddenTrue {
  right: -100px; }
.hiddenFalse {
  right: 20px; }

@media #{$tablet} {
  .actionButton {
    width: 8vw;
    height: 8vw; } }

@media #{$desktop} {
  .actionButton {
    width: 4vw;
    height: 4vw;
    bottom: 120px; } }
